li {
  list-style: none;
}

.activityItem {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.85);
  border-radius: 20px;
  margin-right: 30px;
}

.activityItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
}

.activityItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 70px;
  font-size: 24px;
  font-weight: bold;
}

.Icon:hover {
  color: #4caf50;
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  right: 0;
  top: -15px;
}

.Icon-delete:hover {
  color: red;
}
